:import('Styles/palette.css') {
  i__const_darkColor_51: darkColor;
  i__const_primaryColor_52: primaryColor;
  i__const_neutralColorDark_53: neutralColorDark;
  i__const_neutralColorMedium_54: neutralColorMedium;
  i__const_whiteColor_55: whiteColor;
  i__const_pinkRed_56: pinkRed;
}

:export {
  colors: 'Styles/palette.css';
  darkColor: i__const_darkColor_51;
  primaryColor: i__const_primaryColor_52;
  neutralColorDark: i__const_neutralColorDark_53;
  neutralColorMedium: i__const_neutralColorMedium_54;
  whiteColor: i__const_whiteColor_55;
  pinkRed: i__const_pinkRed_56;
}

.title, .subtitle, .header {
  margin: 0;
}

.p {
  color: i__const_darkColor_51;
}

.label {
  opacity: 0.5;
  font-size: 14px;
  margin-bottom: 2px;
}

.highlight {
  color: i__const_primaryColor_52;
}

.secondary {
  color: i__const_neutralColorDark_53;
}

.disabled {
  color: i__const_neutralColorMedium_54;
  cursor: not-allowed;
}

.inverted {
  color: i__const_whiteColor_55;
}

.smallText {
  font-size: 13px;
}

.lead {
  font-size: 18px;
}

.bolder {
  font-weight: bold;
}

.lighter {
  font-weight: 400;
}

.error {
  color: i__const_pinkRed_56;
}

.fontFamily{
  font-family: 'open-sans';
}



