:export {
  cardShadow: i__const_cardShadow_42;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .4);
  display: -webkit-box;
