          align-items: center;
  padding: 0 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.enter {
  opacity: 0.1
}

.enter.enterActive {
  opacity: 1;
  -webkit-transition: 300ms;
  transition: 300ms;
}

.leave {
  opacity: 1
}

