:import('Styles/common.css') {
  i__const_cardShadow_43: cardShadow
}

:import('Styles/palette.css') {
  i__const_neutralColorLight_44: neutralColorLight;
  i__const_primaryColor_45: primaryColor;
  i__const_neutralColorMedium_46: neutralColorMedium
}

:export {
  colors: 'Styles/palette.css';
  cardShadow: i__const_cardShadow_43;
  neutralColorLight: i__const_neutralColorLight_44;
  primaryColor: i__const_primaryColor_45;
  neutralColorMedium: i__const_neutralColorMedium_46
}

.menu {
  display: block;
  position: relative;
  outline: none
}

.menu:focus {
  outline: none;
}

.list {
  display: block;
  min-width: 120px;
  background-color: i__const_neutralColorLight_44;
  padding: 0;
  margin: 0;
  list-style: none;
  border-radius: 2px;
  overflow: hidden;
  -webkit-box-shadow: i__const_cardShadow_43;
          box-shadow: i__const_cardShadow_43;
  position: absolute;
  top: 24px;
  top: 1.5rem;
  -webkit-animation-name: fadeInDown;
          animation-name: fadeInDown;
  -webkit-animation-duration: 200ms;
          animation-duration: 200ms;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.icon {
  display: block
}

.icon:hover {
  color: i__const_primaryColor_45;
}

.icon:focus,
  .icon svg:focus {
  outline: none !important;
}

.active {
  color: i__const_primaryColor_45 !important;
}

.item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  cursor: pointer;
  padding: 10px 20px;
  margin: 0
}

.item:hover {
  background-color: i__const_neutralColorMedium_46;
}

.itemIcon {
  margin-right: 10px;
}

.option {
  margin: 0;
}

.rightMenu {
  left: 0;
}

.leftMenu {
  right: 0;
}

@-webkit-keyframes fadeInDown {
  from {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0.1;
  }

  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeInDown {
  from {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0.1;
  }

  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}




