:import('Styles/palette.css') {
  i__const_whiteColor_37: whiteColor;
  i__const_neutralColorMedium_38: neutralColorMedium;
}

:export {
  whiteColor: i__const_whiteColor_37;
  neutralColorMedium: i__const_neutralColorMedium_38;
}

.btn {
  width: 50px;
  height: 50px;
  outline: none;
  border-radius: 50%;
  color: i__const_whiteColor_37;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  cursor: pointer;
  border: none;
}

.small {
  width: 20px;
  height: 20px;
}

.medium {
  width: 30px;
  height: 30px;
}

.large {
  width: 40px;
  height: 40px;
}

.disabled {
  background-color: i__const_neutralColorMedium_38;
  pointer-events: none;
}



