:import('Styles/palette.css') {
  i__const_neutralColorMedium_62: neutralColorMedium;
}

:export {
  colors: 'Styles/palette.css';
  neutralColorMedium: i__const_neutralColorMedium_62;
}

.iconContainer {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  outline: none;
}

.onClick {
  cursor: pointer;
  -webkit-transition: 0.3s ease;
  transition: 0.3s ease
}

.onClick:hover {
  color: currentColor;
  opacity: 0.7;
}

.disabled {
  cursor: auto !important;
  color: i__const_neutralColorMedium_62 !important
}

.disabled svg {
  color: i__const_neutralColorMedium_62 !important;
}

.disabled:hover {
  color: i__const_neutralColorMedium_62 !important;
  opacity: 1;
}

.disabled:hover svg {
  color: i__const_neutralColorMedium_62 !important;
  opacity: 1;
}



